:root {
  --white-color: #fff;
  --description-item-color: rgba(18, 20, 23, 0.5);
  --black-bold-color: #121417;
  --filter-select-color: rgb(247, 247, 251);
}

@import url(./components/FilterContainer/FilterContainer.css);

@font-face {
  font-family: 'Inter-500';
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url('./fonts/Inter-Medium.ttf') format('truetype');
}

@font-face {
  font-family: 'Manrope-400';
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url('./fonts/Manrope-Regular.ttf') format('truetype');
}

@font-face {
  font-family: 'Manrope-500';
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url('./fonts/Manrope-Medium.ttf') format('truetype');
}

@font-face {
  font-family: 'Manrope-600';
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: url('./fonts/Manrope-SemiBold.ttf') format('truetype');
}

body {
  /* background-color: var(--white-color); */
  /* background-color: grey; */
  background-size: auto 100%;
  background-position: center center;
  background-repeat: no-repeat;
  font-family: Manrope-400, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
  font-style: normal;
  color: var(--description-item-color);
  width: 100%;
  /* height: 100vh; */
  margin: 0;
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin-top: 0;
  margin-bottom: 0;
}
a {
  text-decoration: none;
}
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
img {
  display: block;
}
button {
  cursor: pointer;
}

.container {
  max-width: 1214px;
  padding: 0 15px;
  margin: 0 auto;
  /* background-color: aqua; */
}
